<template>
  <div class="forget">
    <div class="top"></div>
    <div class="forget-box">
      <van-nav-bar @click-right="goHome">
        <template #left>
          <div class="left-text">
            <div class="logo">
              <img
                class="auto-img"
                src="../assets/images/home_active.png"
                alt=""
              >
            </div>
            <div class="logo-title">Luckin Coffce</div>
          </div>
        </template>
        <template #right>
          <div class="close-box">先逛一逛</div>
        </template>
      </van-nav-bar>
      <div class="login-form">
        <div class="title">忘记密码!</div>
        <div class="en-title">Forget the password</div>

        <div class="login-form-box">
          <van-form>
            <van-field
              type="text"
              class="list"
              label="邮箱："
              placeholder="邮箱"
              :disabled="isChange"
              v-model="userInfo.email"
              @input="send"
            />
            <van-field
              type="text"
              class="list emailCode"
              label="验证码："
              placeholder="请输入邮箱验证码"
              v-model="userInfo.validCode"
            >
              <template #button>
                <van-button
                  size="small"
                  color="#7c90cf"
                  :disabled="isSend"
                  @click="sendVaildCode"
                >{{text}}</van-button>
              </template>
            </van-field>
            <div class="clearfix forgot">
              <span
                class="fr forgottext"
                @click="goLogin"
              >已有账号,立即登录</span>
            </div>
            <div class="login-btn">
              <van-button
                round
                block
                native-type="submit"
                color="#0C34BA"
                @click="commit"
              >下一步</van-button>
            </div>
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/forget.less";
import { validForm } from "../assets/js/ValidForm";
import { utils } from "../assets/js/Utils";

export default {
  data() {
    return {
      // 登录信息
      userInfo: {
        phone: "",
        password: "",
        email: "",
        validCode: "",
      },
      isPassword: true,
      text: "发送验证码",
      isSend: true,
      isChange: false,
    };
  },

  created() {},

  methods: {
    // 去首页
    goHome() {
      this.$router.push({ name: "Home" });
    },

    // 去登录
    goLogin() {
      this.$router.push({ name: "Login" });
    },

    // 开启发送验证码通道
    send() {
      this.isSend = false;
    },
    // 发送验证码
    sendVaildCode() {
      // 发起获取邮箱验证码请求
      //验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      //参数序列化
      let data = utils.queryString({
        email: this.userInfo.email,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          this.$notify({
            type: "warning",
            message: result.data.msg,
          });
          if (result.data.code == "J001") {
            let time = 60;
            this.text = time + "s后重新发送";
            this.isSend = true;
            this.isChange = true;

            let timer = setInterval(() => {
              if (time == 0) {
                clearInterval(timer);
                this.isSend = false;
                this.isChange = false;
                this.text = "发送验证码";
              } else {
                time--;
                this.text = time + "s后重新发送";
              }
            }, 1000);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 提交
    commit() {
      // 验证修改密码表单
      let o = {
        email: {
          value: this.userInfo.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errorMsg: "邮箱格式不正确",
        },
        validCode: {
          value: this.userInfo.validCode,
          reg: /^\d{6}$/,
          errorMsg: "验证码为六位数字",
        },
      };

      if (!validForm.valid(o)) {
        // 验证不通过
        return;
      }

      // 发起忘记密码请求
      // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        validCode: this.userInfo.validCode,
      });

      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "K001") {
            // 验证码验证成功
            this.$router.push({ name: "Retrieve" });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>
